import { CarouselOptions, initCarouselParams } from "../../objects/carousel";
import { responsiveQuery } from "../../settings/responsive";
import { initCarousel } from '../../objects/carousel';
import { CLASS_INITIALISED } from './../../tools/constants/classes';

// Selectors
const SELECTOR_CONTAINER = '[data-charity-carousel-container]';
const SELECTOR_CAROUSEL = '[data-charity-carousel]';
const SELECTOR_NAVIGATION = '[data-charity-carousel-nav]';

// Carousel options
const options:CarouselOptions = {
  slidesPerView: 1.5,
  loop: true,
  centered: false,
  duration:850,
  breakpoints: {
    ['(min-width: 550px)']: {
      slidesPerView: 1.8
    },
    [responsiveQuery('SMALL')]: {
      slidesPerView: 2.4,
      centered:true
    },
    [responsiveQuery('MEDIUM')]: {
      slidesPerView: 4,
      centered: true
    }
  }
}


// Initialise sections
document.querySelectorAll<HTMLElement>(SELECTOR_CONTAINER).forEach(section => {

  // Check if section has already been initialized
  if(section.classList.contains(CLASS_INITIALISED)) return;
  section.classList.add(CLASS_INITIALISED);

  // Check if carousel element exists
  const carouselElement = section.querySelector<HTMLElement>(SELECTOR_CAROUSEL);
  if(!carouselElement) return;

  // Set params for carousel
  const params:initCarouselParams = { container: carouselElement, options };
  const navigationContainer = section.querySelector<HTMLElement>(SELECTOR_NAVIGATION);
  if(navigationContainer) params.navigationContainer = navigationContainer;


  // Initialize carousel
  initCarousel(params);
});